.banner-text {
    position: absolute;
    width: 600px;
    height: 150px;
    left: calc(50% - 600px/2 + 300px);
    top: 200px;

    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 78px;
    display: flex;
    align-items: center;
}

.banner-img {
    position: absolute;
    height: 400px;
    left: 0px;
    right: 0px;
    top: 75px;

    background: url(../assets/images/banner-ingenieria.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.banner-container{
    height: 400px;
}

@media only screen and (max-width: 768px) {
    .banner-text {
        position: absolute;
        width: 600px;
        height: 150px;
        left: 1rem;
        top: 200px;

        font-size: 36px;
        line-height: 42px;
    }
}