.qs-title {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 400;
    font-size: 144px;
    line-height: 213px;
    display: flex;
    align-items: center;
    text-align: right;
}

.qs-background {
    position: absolute;
    padding: 1rem;
    /* min-height: 600px; */
    left: 50%;
    right: 0px;
    top: 550px;

    background: #F2CB05;
}

@media only screen and (max-width: 768px) {
    .qs-title {
        font-size: 96px;
        line-height: 121px;
        margin-bottom: 1rem;
        align-items: center;
        text-align: center;
    }
}

