.background-nh {
    width: 100%;
    background: #F2B705;
}

.nh-container{
    margin-top: 1rem;
    margin-bottom: 2rem;
}

.nh-title {
    font-family: 'Oswald';
    font-style: normal;
    font-weight: 400;
    font-size: 121px;
    line-height: 198px;
}

@media only screen and (max-width: 768px) {
    .nh-title {
        font-size: 96px;
        line-height: 121px;
        margin-bottom: 1rem;
    }
}